.btn {
    padding: 5px 15px;
    border-radius: 20px;
    cursor: pointer;
    transition: 0.2s opacity;

    &:hover {
        opacity: 0.8;
    }
}
.btn-danger {
    background-color: var(--red-1);
    color: white;
    border: 2px solid var(--red-1);
}
.btn-primary {
    background-color: var(--primary);
    color: white;
    border: 2px solid var(--primary);
}

.btn-reverse-primary {
    border: 2px solid var(--primary);
    color: var(--primary);
    background-color: white;
}

.card {
    border-radius: 20px;
    background-color: white;
}
.text-primary {
    color: var(--primary);
}
label {
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 3px;
}
input {
    padding: 10px 15px;
    border: 2px solid var(--gray-2);
    border-radius: 20px;
}
.form-error {
    margin: 0;
    color:  var(--red-1);
    font-size: 12px;
    font-weight: 500;
}