:root {
    --primary: #ff6348;
    --gray-1: #20232a;
    --gray-2: #efefef;
    --red-1: #e74c3c;
    --font-family: 'Montserrat', sans-serif;
    --box-shadow: 0 6px 9px 0 rgb(0 0 0 / 15%) ;
    --box-shadow-nav: rgb(0 0 0 / 22%) 0px 2px 18px 0px;
    --text-color: #444;
    --border: 1px solid var(--gray-2);
    --border-radius: 4px;
}